#root {
    max-width: 1280px;
    margin: 0 auto;
    padding: 2rem;
    text-align: center;
}


body {
    overflow: hidden;
}


@keyframes logo-spin {
    from {
        transform: rotate(0deg);
    }
    
    to {
        transform: rotate(360deg);
    }
}
  

@media (prefers-reduced-motion: no-preference) {
    a:nth-of-type(2) .logo {
        animation: logo-spin infinite 20s linear;
    }
}


.card {
    padding: 2rem;
}


.read.the-docs {
    color: #888;
}


input[type=radio] {
    display: none;
}


.star {
    cursor: pointer;
}


.innerDiv {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


.stars {
    display: flex;
}


button{
    width: 109px;
    height: 37px;
    font-size: 14px;
    font-weight: 600;
    position: absolute;
    bottom: 16px;
    right: 97px;
    border-radius: 9px;
    background-color: #0F71FF;
    color: #fff;
}   


.modal {
    background-color: #fff;
    border-radius: 10px;
    width: 293px;
    height: 253px;
    position: absolute;
    top: 35%;
    left: 42%;
}


img {
    margin: 0 0 0 49px;
}


p {
    color: #fff;
}


.close {
    position: absolute;
    bottom: 16px;
    right: 92px;
}